body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* COLORS */
.App {
  text-align: center;
  padding-top: 30px;
  color: white;
  font-family: 'Kalam', cursive;
  display: grid;
  justify-content: center; }

body {
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /* height: 100vh; */
  /* background-color: $purpleDark; */
  background-image: -webkit-gradient(linear, left bottom, right top, from(#462a3e), to(#8c4a58));
  background-image: linear-gradient(to right top, #462a3e, #8c4a58); }

/* COLORS */
.searchInput {
  font-size: 1rem;
  padding: 15px 50px;
  text-align: center;
  border: 3px solid #f4b15d;
  border-radius: 25px;
  background-color: black;
  color: white;
  outline: none; }

.searchInput::-webkit-input-placeholder {
  color: white; }

.searchInput::-moz-placeholder {
  color: white; }

.searchInput::-ms-input-placeholder {
  color: white; }

.searchInput::placeholder {
  color: white; }

.searchInput:focus {
  animation: changingColors .8s ease-in-out infinite alternate;
  /* Safari 4.0 - 8.0 */
  -webkit-animation: changingColors .8s ease-in-out infinite alternate; }

@-webkit-keyframes changingColors {
  from {
    border: 3px solid #f4b15d; }
  to {
    border: 3px solid #0d0814;
    box-shadow: 0 0 5px #f9f871; } }

@keyframes changingColors {
  from {
    border: 3px solid #f4b15d; }
  to {
    border: 3px solid #0d0814;
    box-shadow: 0 0 5px #f9f871; } }

/* COLORS */
/* Welcome Site Drink Photo */
.introPhoto {
  width: 50%;
  margin: 0 auto; }

.drinkArt {
  width: calc(15% + 100px); }

ul {
  padding: 0; }

/* ================================= 
                HEADER
====================================*/
#mainHeader {
  padding: 15px 0;
  font-family: 'Goudy Bookletter 1911', serif; }

.mainHeaderHeading {
  font-size: calc(20px + .96vw); }

.mainHeaderHeading::first-letter {
  color: #f4b15d; }

/* ================================= 
        Main Section Container
====================================*/
#mainSectionContainer {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: "mainSectionInformations" "mainSectionIngredients"; }

/* ================================= 
        Ingredients Section
====================================*/
#mainSectionIngredients {
  grid-area: mainSectionIngredients;
  display: flex;
  flex-flow: row-reverse;
  justify-content: center;
  margin: 16px 5px; }

.ingredientList,
.amountOfIngredientList {
  list-style: none;
  text-align: left;
  padding: 20px;
  margin: 0;
  background-color: #0d0814; }

.ingredientList {
  border: 2px solid #f4b15d;
  border-left: 0;
  border-left: 1px dotted white; }

.amountOfIngredientList {
  border: 2px solid #f4b15d;
  border-right: 0; }

.ingredientListHeading,
.amountOfIngredientListHeading {
  font-size: calc(20px + .76vw); }

.amountOfIngredientListHeading {
  white-space: nowrap; }

.ingredientListItem,
.amountOfIngredientListItem {
  font-size: calc(10px + .40vw);
  padding-bottom: 2rem; }

/* ================================= 
        Ingredients Section
          Vocabulary Menu
====================================*/
.vocabularyMenu {
  display: none;
  background-color: #0d0814;
  border: 2px solid #f4b15d; }

.vocabularyMenu label {
  display: flex;
  cursor: pointer;
  -webkit-transition: 1s;
  transition: 1s;
  border-top: 1px solid #f4b15d; }

.vocabularyMenu label:hover {
  background-color: #8c4a58; }

.vocabularyMenu input:checked ~ label:hover {
  background-color: #462a3e; }

.vocabularySpan {
  -webkit-writing-mode: vertical-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: upright;
          text-orientation: upright;
  align-self: center;
  width: 25px;
  margin-right: 5px; }

.closeVocabularySpan {
  align-self: center;
  padding: 0 5px; }

.vocabularyMenu input {
  display: none; }

.vocabularyMenu input:checked ~ label span:last-child {
  display: none; }

.vocabularyMenu input:not(:checked) ~ label span:first-child {
  display: none; }

.vocabularyItemsList {
  overflow: hidden;
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  /* transition: width 0.5s, height 0.5s; */
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  list-style: none;
  font-size: 0; }

.vocabularyItemsListHeading {
  padding-top: 30px;
  padding-bottom: 4px; }

.vocabularyMenu input:checked ~ ul {
  width: 12.5rem;
  height: 100%;
  border-right: 2px solid #f4b15d;
  font-size: calc(10px + 0.35vw); }

.vocabularyMenu input:checked ~ label {
  background-color: #8c4a58; }

/* ================================= 
        Vocabulary Lite Version
====================================*/
.vocabularyLite {
  font-size: calc(10px + 0.35vw);
  display: flex;
  justify-content: center; }

.vocabularyLite h1 {
  margin: 0; }

.vocabularyItemsListLite {
  padding: 20px calc(90px + 1.5vw);
  margin-top: 0;
  list-style: none;
  border: 2px solid #f4b15d;
  background-color: #0d0814; }

/* ================================= 
        Informations Section
====================================*/
#mainSectionInformations {
  grid-area: mainSectionInformations;
  align-self: center;
  /*     margin: 16px 5px;
    padding: 20px; */
  padding: 5px 20px;
  position: relative; }

/* stworzenie bordera poziomego */
#mainSectionInformations::before {
  content: '';
  background-color: #f4b15d;
  position: absolute;
  /* display: block; */
  height: 2px;
  left: 50%;
  width: 95%;
  bottom: 0;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0); }

.instructionsHeading {
  text-decoration: underline;
  -webkit-text-decoration-color: #f4b15d;
          text-decoration-color: #f4b15d;
  font-size: calc(20px + .96vw); }

.instructionsHeading::first-letter {
  color: #f4b15d; }

#mainSectionInformations h2 {
  color: #f4b15d; }

