.glassToServeContainer h2,
.howToDoContainer h2 {
  display: inline;
  font-size: calc(10px + .62vw); }

.glassToServeContainer span,
.howToDoContainer span {
  font-size: calc(10px + .62vw); }

.glassToServeHeading::before,
.howToDoHeading::before {
  font-family: "Font Awesome 5 Free";
  font-size: calc(10px + .57vw);
  color: white;
  position: relative; }

.glassToServeHeading::before {
  content: '\F57B';
  right: 2%;
  bottom: 2px; }

.howToDoHeading::before {
  content: '\F059';
  right: 2%;
  bottom: 1px; }

.howToDoContainer {
  padding: 0 30px;
  padding-bottom: 10px; }

.drinkPhoto {
  width: 30%;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  border-radius: 5%;
  box-shadow: 0 0 10px #fff; }

