/*=============================
          Media Queries 
 ============================*/
@media only screen and (min-width: 768px) {
  .vocabularyMenu {
    display: flex; }
  .vocabularyLite {
    display: none; } }

@media only screen and (min-width: 1224px) {
  #mainSectionContainer {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: 'mainSectionIngredients mainSectionInformations'; }
  #mainSectionInformations::after {
    content: "";
    background-color: #f4b15d;
    position: absolute;
    top: 0;
    left: 0;
    height: 95%;
    width: 2px; }
  #mainSectionInformations {
    grid-area: mainSectionInformations;
    align-self: center;
    margin: 16px 5px;
    padding: 20px 50px;
    position: relative; }
